body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  background:  url('./img/fundo.png'), linear-gradient(144deg, #af40ff, #5b42f3 50%, #00ddeb);
  position: relative;
  background-size: cover;
  /* background-color: #30d599; #00ddeb */
  /* #32bd8a */
  width: 100%;
  min-height: 100vh;
}
